.svg-dag .clusters rect {
  fill: #FFF;
  stroke: #4cae4c;
  stroke-width: 1.25;
  rx: 3px;
  ry: 3px;
}

.svg-dag text {
  font-weight: 300;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serf, serif;
  font-size: 14px;
  stroke: none;
  fill: #000;
}

.svg-dag .node rect {
  rx: 3px;
  ry: 3px;
  fill: #DDD;
  stroke: #CCC;
}

.svg-dag .node:hover rect {
  fill-opacity: 0.7;
  cursor: pointer;
}

.svg-dag .node text {
  fill: #000;
}

.svg-dag .node.SUCCESS rect {
  fill: #5cb85c;
  stroke: #4cae4c;
}

.svg-dag .node.SUCCESS text {
  fill: #FFF;
}

.svg-dag .node.FAILED rect {
  fill: #d2322d;
  stroke: #d2322d;
}

.svg-dag .node.FAILED text {
  fill: #FFF;
}

.svg-dag .node.PENDING rect {
  fill: #DDD;
  stroke: #CCC;
}

.svg-dag .node.PENDING text {
  fill: #000;
}

.svg-dag .node.RUNNING rect {
  fill: #39b3d7;
  stroke: #39b3d7;
}

.svg-dag .node.RUNNING text {
  fill: #FFF;
}

.svg-dag .toggle-link {
  cursor: pointer;
  stroke: #4cae4c;
}

.svg-dag .toggle-link.expanded {
  fill: #ccc;
}

.svg-dag .toggle-link.expanded:hover {
  fill: #5cb85c;
}

.svg-dag .toggle-link.collapsed {
  fill: #fff;
}

.svg-dag .toggle-link.collapsed:hover {
  fill: #5cb85c;
}

.svg-dag .edgePath path {
  stroke: #CCC;
  stroke-width: 1.5;
}

.svg-dag .edgePath defs marker {
  fill: #CCC;
}

.tipsy {
  font-size: 12px;
  position: absolute;
  padding: 5px;
  z-index: 100000;
}

.tipsy-inner {
  background-color: #282828;
  color: #FFF;
  padding: 8px;
  text-align: center;
  border-radius: 8px;
}

.tipsy-arrow {
  position: absolute;
  width: 0;
  height: 0;
  line-height: 0;
  border: 5px dashed #282828;
}

.tipsy-arrow-n {
  border-bottom-color: #282828;
}

.tipsy-arrow-s {
  border-top-color: #282828;
}

.tipsy-arrow-e {
  border-left-color: #282828;
}

.tipsy-arrow-w {
  border-right-color: #282828;
}

.tipsy-n .tipsy-arrow {
  top: 0;
  left: 50%;
  margin-left: -5px;
  border-bottom-style: solid;
  border-top: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-nw .tipsy-arrow {
  top: 0;
  left: 10px;
  border-bottom-style: solid;
  border-top: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-ne .tipsy-arrow {
  top: 0;
  right: 10px;
  border-bottom-style: solid;
  border-top: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-s .tipsy-arrow {
  bottom: 0;
  left: 50%;
  margin-left: -5px;
  border-top-style: solid;
  border-bottom: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-sw .tipsy-arrow {
  bottom: 0;
  left: 10px;
  border-top-style: solid;
  border-bottom: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-se .tipsy-arrow {
  bottom: 0;
  right: 10px;
  border-top-style: solid;
  border-bottom: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}

.tipsy-e .tipsy-arrow {
  right: 0;
  top: 50%;
  margin-top: -5px;
  border-left-style: solid;
  border-right: 0;
  border-top-color: transparent;
  border-bottom-color: transparent;
}

.tipsy-w .tipsy-arrow {
  left: 0;
  top: 50%;
  margin-top: -5px;
  border-right-style: solid;
  border-left: 0;
  border-top-color: transparent;
  border-bottom-color: transparent;
}